<template>
  <div style="text-align:center;">
    <h3 class="mdl-color-text--primary">Edit vendor</h3>
    <vendor-form
      ref="form"
      :vendor="vendor">
    </vendor-form>
    <button
      class="mdl-button mdl-js-button mdl-button--raised mdl-js-ripple-effect mdl-button--colored custom-button--primary"
      @click="modifyVendor">
        Update
    </button>
     <div>
      <bank-list-view
        :holder="vendor"
        :type="'vendor'"
      ></bank-list-view>
    </div>

  </div>
</template>
<script>
import { VendorForm } from '.'
import { mapActions } from 'vuex'
import { BankListView } from 'BANKS_APP/components'
import { MESSAGES } from '@/constants'

export default {
  props: {
    vendor: {
      type: Object,
      required: true
    }
  },
  components: {
    VendorForm,
    BankListView
  },
  computed: {
    form: {
      get () {
        return this.$refs.form
      }
    }
  },
  methods: {
    ...mapActions([
      'updateVendor'
    ]),
    modifyVendor () {
      this.form.valid().then(valid => {
        if (valid) {
          this.updateVendor(this.form.vendorParams).then(() => {
            this.$parent.fetchVendors()
            this.$toastr.s(MESSAGES.success_add)
            this.$parent.toggleEditView()
          }).catch(e => {
            this.$toastr.e(MESSAGES.form_verify)
          })
        }
      })
    }
  }
}
</script>



